body {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.button {
  padding: 30px 60px;
  border: 5px solid black;
  transition: 0.2s ease;
  margin-top: 50px;
  cursor: pointer;
}
.button_text {
  font-family: Montserrat;
  font-size: 25px;
  font-weight: bold;
}
.button:hover {
  background: #4affe4;
}
.button:nth-of-type(2):hover {
  background: #ff44d0;
}
.button:nth-of-type(3):hover {
  background: #fffb06;
}
.button:nth-of-type(4):hover {
  background: #00ff73;
}
